// All of the Node.js APIs are available in this process.
require('babel-polyfill');
const $ = require('jquery')
const CDP = require('chrome-remote-interface');
const request = require('request');
const electron = require('electron');

let webSocketDebuggerUrl, targetId, browser;
$('#getPageInfo').on('click', () => {
  //
  request('http://localhost:9223/json', function (error, response, body) {
    if (!error && response.statusCode == 200) {
      body = JSON.parse(body);
      let remotePage = body.length && body[0];
      if (remotePage) {
        webSocketDebuggerUrl = remotePage.webSocketDebuggerUrl;
        targetId = remotePage.id;
      }
    }
  })
  //
  request('http://localhost:9223/json/version', function (error, response, body) {
    if (!error && response.statusCode == 200) {
      console.log(body) // 
    }
  })
})
function connect(options) {
  return new Promise((resolve, reject) => {
    CDP(options, (client) => {
      resolve(client)
    }).on('error', (err) => {
      console.error(err);
    });
  })
}
$('#go').on('click', async () => {
  if (webSocketDebuggerUrl) {
    const options = {
      port: '9223',
      target: targetId
    }
    const { Network, Page } = await connect(options);

      // Network.requestWillBeSent((params) => {
      //   console.log(params.request.url);
      // });
      // Page.loadEventFired(() => {
      //   client.close();
      // });
      await Network.enable();
      await Page.enable();
      await Page.navigate({ url: 'https://github.com' });
  }
})
$('#open').on('click', () => {
  // 
  let win = new electron.remote.BrowserWindow({ width: 400, height: 320 })
  win.on('close', function () {
    // 
    win = null
  })
  // 
  win.loadURL('https://www.baidu.com')
  win.openDevTools({ mode: 'rigth' });
  // 
  win.show()
})